<template>

  <div class="content">
    <div class="modWidth music-search" aria-hidden="true">
      <div class="music-search-box">
        <input type="text" placeholder="搜索音乐" class="music-search-input">
        <button class="music-search-btn fa fa-search fa-2x"></button>
      </div>
    </div><div class="searchList">
    <div class="section_inner">
      <div class="searchType">
        <a href="javascript:;" class="searchKw searchKw_current">单曲</a>
        <a href="javascript:;" class="searchKw">专辑</a>
        <a href="javascript:;" class="searchKw">MV</a>
        <a href="javascript:;" class="searchKw">歌单</a>
        <a href="javascript:;" class="searchKw">用户</a>
        <a href="javascript:;" class="searchKw">歌词</a>
      </div>

    </div></div>
  </div>
</template>

<script>
    export default {
        name: "Search"
    }
</script>

<style scoped>

  /*搜索页样式*/
  .music-search{
    position: relative;
    height: 500px;
    background:url("../../assets/search.png") no-repeat;
    background-position:center -365px ;
    /*background-size: initial;*/
    opacity: .7;
  }
  .music-search-box{
    position: absolute;
    top: 50% ;
    left: 50%;
    width: 520px;
    height: 42px;
    margin-top: -20px;
    margin-left: -250px;
    outline: none;
    text-indent: 10px;
  }
  .music-search-input{
    position: relative;
    top: 0;
    width: 430px;
    height: 44px;
    outline: none;
    border: none;
    text-indent: 10px;
  }
  .music-search-btn{
    position: absolute;
    height: 43.5px;
    width: 40px;
    top: .7px;
    right: 40px;
    border: 0 none;
    background: #ffffff;
    font-size: 30px;
    color: #666;
    outline: none;
  }
  .searchType{
    height: 56px;
    line-height: 56px;
  }
  .searchType .searchKw{
    margin-right: 56px;
    margin-bottom: 30px;
    font-size: 16px;
  }
  .searchKw.searchKw_current{
    color: #31c27c;
  }

</style>



