<template>

  <div class="section-inner main-header">
    <div class="nav-logo">
      <h1>
        <a href="./img/footer.png">Lydia</a>
      </h1>
      <i class="fa fa-apple"></i>
    </div>
    <div class="nav-tag">
      <ul class="nav-tag-ul">
        <li class="nav-tag-item "><router-link to="/" class="nav-tag-lk" exact-active-class="router-high-active" id="MID1">音乐馆</router-link></li>
        <li class="nav-tag-item"><router-link to="/song" class="nav-tag-lk" exact-active-class="router-high-active" id="MID2">歌单</router-link></li>
        <li class="nav-tag-item"><router-link to="/rank" class="nav-tag-lk" exact-active-class="router-high-active" id="MID3">排行榜</router-link></li>
        <li class="nav-tag-item"><router-link to="/singer" class="nav-tag-lk" exact-active-class="router-high-active" id="MID4">歌手</router-link></li>
        <li class="nav-tag-item"><router-link to="/new" class="nav-tag-lk" exact-active-class="router-high-active" id="MID5">最新音乐</router-link></li>
      </ul>
    </div>
    <div class="nav-search">
      <input type="text" id="wrap_search" class="wrap-search" value="搜索音乐,视频,歌词,电台" onfocus="if (value =='搜索音乐,视频,歌词,电台'){value =''}" onblur="if (value ==''){value='搜索音乐,视频,歌词,电台'}">
      <i class="fa fa-search"></i>
    </div>
    <div class="not-login">登陆</div>
  </div>

</template>

<script>
    export default {
        name: "Header",
     methods:{
          search:function (e) {
            if(e.keyCode ===13){
              this.$router.push('./search')
            }
          }
     }
    }
</script>

<style scoped>
.main-header{
  padding-top: 80px;
}
  .section-inner {
    margin: 0 auto;
    max-width: 1200px;
    position: relative;
  }


  @media  (max-width:1240px){
    .section-inner {
      margin: 0 50px;
      min-width: 900px;
      position: relative;
    }

  }



  .nav-logo{
    position: absolute;
    left: 0;
    top: 0;
    font-size: 20px;
    color: #31c27c;
    line-height: 80px;
    height: 80px;
  }
  .nav-logo i{
    position: absolute;
    top: 15px;
    left: 100px;
    font-size: 50px;
    color: #31c27c;
  }
  .nav-logo a{
    color: #31c27c;
    font-size: 30px;
    font-weight: bolder;
  }
  .nav-tag{
    position: absolute;
    display: block;
    top: 0;
    left: 180px;
  }
  .nav-tag>ul{
    height: 80px;
  }
  .nav-tag .nav-tag-item{
    position: relative;
    float: left;
  }
  .nav-tag .nav-tag-item .nav-tag-lk{
    display: block;
    padding: 10px 17px;
    text-align: center;
    line-height: 60px;
    font-size: 14px;
  }


  .nav-search{
    position: absolute;
    left: 600px;
    top: 40px;
    width: 160px;
    height: 36px;

    background: #f3f5f6;
    border-radius: 8px;
    padding-right: 56px;
    margin: -20px;
  }
  .nav-search .fa-search{
    position: relative;
    font-size: 16px;
    line-height: 39px;
    right: -187px;
    top: -36px;
  }
  .nav-search>input{
    padding: 2px 0 2px 15px;
    width: 150px;
    height: 30px;
    line-height: 36px;

    outline: none;
    border: none;
    color: #666666;
    background-color: #f3f5f6;


  }
  .user{

  }
  .not-login {
    height: 80px;
    position: absolute;
    right: 0;
    top: 0;
    width: 70px;
    line-height: 80px;
    font-size: 14px ;
    text-align: right;
    padding-right: 15px;
  }
  .not-login:hover {
    color: #31c27c;
  }
.router-high-active{
  background: #31c27c;
  color: #ffffff;
}
.nav-tag-lk:hover{
  color: #31c27c;
}
  .nav-tag-lk.router-high-active{
    color: #ffffff;
  }


</style>



