import Home from './components/home/Home';
import Song from './components/song/Song';
import Rank from './components/rank/Rank';
import Singer from './components/singer/Singer';
import New from './components/new/New';
import Login from './components/login/Login';
import Search from './components/search/Search';
import Detail from './components/search/Search';
export const routes = [
  {path:'/',component: Home,name:'homeLink'},
  {path:'/song',component: Song,name:'songLink'},
  {path:'/rank',component: Rank,name:'rankLink'},
  {path:'/singer',component: Singer,name:'singerLink'},
  {path:'/new',component: New,name:'newLink'},
  {path:'/login',component: Login,name:'loginLink'},
  {path:'/search',component: Search,name:'searchLink'},
  {path:'/detail',component:Detail,name:'detailLink'},
  {path:'*',redirect:'/'},
];



