import Vue from 'vue'
import  Vuex from 'vuex'
Vue.use(Vuex);
export const  store = new Vuex.Store({
  state:{
    musicDetailData:[
      {
        imgurl: "http://p.qpic.cn/music_cover/xiabfMZAmQ0PYUzgCvOicArAC8POrEnKRV6YrfH9U8NvCn4yeGic85oXQ/600?n=1",
        score: 0.0,
        dissid: "7078535043",
        createtime: "2019-07-05",
        creator: {
          name: "QQ音乐官方歌单",
          type: 6,
          isVip: 6
        },
        listennum: 38916,
        dissname: "最·Alan Walker",}
    ]
  },
  getters: {
    getMusicData: ((state) => {
      var musicNewData = state.musicDetailData.map(songData=>{
        return{
          imgurl: songData.imgurl,
          dissid: songData.dissname,
          createtime: songData.createtime,
          creator: {
            name: songData.creator.name,
            type:songData.creator.type ,
            isVip: songData.creator.isVip
          },
          listennum: songData.listennum,
          dissname: songData.dissname,
        };
      });
      return musicNewData;
    })

}
});







