<template>
  <keep-alive>
    <div class="slider-wrap J_sliderWrap">
    <ul class="list" >
      <li class="item fl-3 right" >
        <a href="javascript:;" class="lk">
          <img alt="" class="banner" :src="banner1">
        </a>
      </li>
      <li class="item fl-2 right">
        <a href="javascript:;" class="lk">
          <img alt="" class="banner" :src="banner2">
        </a>
      </li>
      <li class="item fl-1">
        <a href="javascript:;" class="lk">
          <img alt="" class="banner" :src="banner3">
        </a>
      </li>
      <li class="item fl-2 left">
        <a href="javascript:;" class="lk">
          <img alt="" class="banner" :src="banner4">
        </a>
      </li>
      <li class="item fl-3 left">
        <a href="javascript:;" class="lk">
          <img alt="" class="banner" :src="banner5">
        </a>
      </li>
    </ul>
    <div class="banner-mask"></div>

  </div>
  </keep-alive>
</template>

<script>
import {_request} from "../../../util/tools";

export default {
        name: "Slider",
      data(){
          return{
            list:[],
            banner1:'',
            banner2:'',
            banner3:'',
            banner4:'',
            banner5:''

          }
      },
      methods:{
        bannerInit:function () {
          let Slider = new class{
            constructor(){
              this.t = null;
              this.oItems  = document.getElementsByClassName('item');
              this.slider   = document.getElementsByClassName('J_sliderWrap')[0];
              this._arr = ['item fl-3 right', 'item fl-2 right', 'item fl-1', 'item fl-2 left','item fl-3 left'];
            }
            init() {
              this.bindEvent();
               // this.initArr();
              this.autoPlay();

            }
            bindEvent() {
              this.slider.addEventListener('mouseenter',this.mouseIn.bind(this),false);
              this.slider.addEventListener('mouseleave',this.mouseOut.bind(this),false);
            }
            initArr() {
              this._forEachItem(this.oItems, this._arr, 'initArr')
            }
            autoPlay() {
              this.t = setInterval(this._slide.bind(this), 5500)
            }
            mouseIn(){
              clearInterval(this.t)
            }
            mouseOut(){
              this.autoPlay();
            }
            _slide() {
              let arr = this._arr;
              arr.push(arr[0]);
              arr.splice(0, 1);
              this._forEachItem(this.oItems, this._arr, 'change class')
            }
            _forEachItem(items, arr, type) {
              items.jForEach(function (val, idx, selfArr) {
                if (type === 'initArr') {
                  arr.push(val.className);
                  console.log(val.className);
                } else if (type === 'change class') {
                  val.className = arr[idx];
                }
              }, this);
            }

          }();
          Slider.init();
        }
      },
      async mounted() {
          let data = await _request('netease','banner');
        data.data.slice(0,5).forEach((val,index)=>{
          this.list.push(val.picUrl)
        });
      this.banner1 = this.list[0];
        this.banner2 = this.list[1];
        this.banner3 = this.list[2];
        this.banner4 = this.list[3];
        this.banner5 = this.list[4];
        this.bannerInit()

      }
    }
</script>

<style scoped>
  .slider-wrap{
    max-width: 900px;
    height: 180px;
    margin: 20px auto;
    background-color: #f5f5f5;
    position: relative;
  }
  .slider-wrap .list{
    height: 100%;
    position: relative;
  }
  .slider-wrap>ul>li img{
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
  }
  .slider-wrap .item{
    position: absolute;
    border-color: #f3f5f6;
    transition: all .5s cubic-bezier(.3,.5,.1,.9);
  }
  .slider-wrap .item.fl-1{
    width: 475px;
    height: 190px;
    top: 0;
    left: 212px;
    z-index: 3;

    outline: none;
    border-width: 0;
  }
  .slider-wrap .item.fl-2{
    height: 180px;
    width: 450px;
    z-index: 1;
    outline: none;
    border-color: transparent;
    top: 10px;
    border-width: 0;
  }
  .slider-wrap.item.fl-3{
    height: 140px;
    width: 450px;
    border-width: 0;
  }
  .slider-wrap .item.fl-2.right{
    left: 450px;
  }

  .slider-wrap .item.fl-2.left{
    left: 0;
  }
  .slider-wrap .item.fl-3{
    top: 10px;
    z-index: 0;
    overflow: hidden;
    width: 450px;
    height: 140px;
    opacity: 0;
    border-width: 0;

  }
  .slider-wrap .item.fl-3.right{
    left: 450px;
  }
  .slider-wrap .item.fl-3.left{
    left: 0;

  }

  .banner-mask{
    position: absolute;
    width: 900px;
    height: 180px;
    margin: 20px auto;
    background: rgba(0, 0, 0, 0.4);
    top: -10px;
    left: 0;
    z-index: 2;
  }

</style>



