//判断数组方法
  isArrayFn:function(value){
  if (typeof Array.isArray === "function") {
    return Array.isArray(value);
  }else{
    return Object.prototype.toString.call(value) === "[object Array]";
  }
},

//数组去重
Aunique:Array.prototype.unique = function(){
  var temp = {},
    newArr= [] ;
  for(var i = 0;i < this.length;i++){
    if(!temp.hasOwnProperty(this[i])){
      temp[this[i]] = this[i];
      newArr.push(this[i])
    }
  }
  return newArr
},
//字符串去重
Sunique:String.prototype.unique = function(){
  var temp = {},
    newStr= '';
  for(var i = 0;i < this.length;i++){
    if(!temp.hasOwnProperty(this[i])){
      temp[this[i]] = this[i];
      newStr += this[i]
    }
  }
  return newStr
},
//Map封装
AjMap:Array.prototype.jMap = function(fn){
  var arr = this,
    len = arr.length,
    arg2 = arguments[1]  || window,
    nArr = [],item;
  for(var i = 0; i < len; i++){
    item = tools.deepClone(arr[i]);
    nArr.push(fn.apply(arg2,[item,i,arr]))
  }
  return nArr;
},
AjForEach:Array.prototype.jForEach = function (fn) {
  var arr = this,
    len = arr.length,
    arg2 = arguments[1]  || window;
  for(var i = 0; i < len; i++){
    fn.apply(arg2,[arr[i],i,arr]);
  }
},
AjFilter:Array.prototype.jFilter = function (fn) {
  var arr = this,
    len = arr.length,
    arg2 = arguments[1]  || window,
    nArr = [],
    nItem;

  for(var i = 0; i < len; i++){
    nItem = tools.deepClone(arr[i],{});
    fn.apply(arg2,[nItem,i,arr]) ? nArr.push(nItem): '';
  }
  return nArr;
},
AjReduce:Array.prototype.jReduce = function(fn,initialVal){
  var arr = this,
    len = arr.length,
    arg2 = arguments[2] || window;
  for (var i = 0; i < len; i++){
    initialVal = fn.apply(arg2,[initialVal,arr[i],i,arr]);
  }
  return initialVal;
},
HjForEach:HTMLCollection.prototype.jForEach = function (fn) {
  var arr = this,
    len = arr.length,
    arg2 = arguments[1]  || window;
  for(var i = 0; i < len; i++){
    fn.apply(arg2,[arr[i],i,arr]);
  }
},
//函数柯里化
  curry:function (fn,len) {
  var len = len || fn.length;
  var func = function (fn) {
    var _arg = [].slice.call(arguments,1);
    return function () {
      var newArgs = _arg.concat([].slice.call(arguments));
      return fn.apply(this,newArgs)
    }
  };
  return function () {
    var argLen = arguments.length;
    if(argLen < len){
      //    [2,3] [fn].concat([2,3]) ---> [fn,2,3]
      var formatedArr = [fn].concat([].slice.call(arguments));
      return curry(func.apply(this,formatedArr),len - argLen);
    }else{
      return fn.apply(this,arguments);
    }
  }
},
//函数防抖,triggerNow值为false/true
  debounce:function(fn,time,triggerNow) {
  var t = null;
  var debounced = function () {
    var _self =this,
      args = arguments;
    if(t) {
      clearInterval(t)
    }
    if(triggleNow){
      var exec = !t;
      t =setTimeout(function () {
        t = null;
      },time);
      if(exec){
        fn.apply(_self,args)
      }
    }else{
      t = setTimeout(function () {
        fn.apply(_self,args)
      },time);
    }
  };
  debounced.remove = function () {
    clearTimeout(t);
    t = null ;
  };
  return debounced;
},
//函数节流
  throttle:function (fn,delay) {
  var t = null,
    begin = new  Date().getTime();
  return function () {
    var _self = this,
      args = arguments,
      cur = new  Date().getTime();
    clearTimeout(t);
    if(cur - begin >= delay){
      fn.apply(_self,args);
      begin = cur;
    } else{
      t = setTimeout(function () {
        fn.apply(_self,args) ;
      },delay)
    }
  }
},


