   import axios from 'axios'
const _request=function (mode, key,params){
  var  url= 'https://v1.itooi.cn';
  return new Promise((resolve, reject) =>{
    axios.get(url+'/'+mode+'/'+key, {
      params: params
    }).then(res =>{
      resolve(res.data);
    }).catch(err =>{
      console.log(err);
    })
  })
}
const  HForEach = HTMLCollection.prototype.jForEach = function (fn) {
  var arr = this,
    len = arr.length,
    arg2 = arguments[1]  || window;
  for(var i = 0; i < len; i++){
    fn.apply(arg2,[arr[i],i,arr]);
  }
};
export {_request,HForEach}



